
.registration-container {
    max-width: 400px;
    max-height: 200px;
    margin: 0 auto;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color:sienna;
  }
  
  .registration-container h2 {
    margin-bottom: 20px;
    text-align: center;
  }
  
  .registration-container form {
    display: flex;
    flex-direction: column;
  }
  
  .registration-container input,
  .registration-container button {
    margin-bottom: 15px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .registration-container .error-message {
    color: red;
    margin-bottom: 10px;
  }
  
  .registration-container p {
    text-align: center;
  }
  
  .registration-container p a {
    color: peru;
    text-decoration: underline;
  }
  .registration-container {
    background-image: url('pic1.jpg'); /* Replace 'path_to_your_image.jpg' with the actual path to your image */
    background-size: cover;
    background-position: center;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    color: white; /* Set text color to white or a contrasting color */
  }
  