.container {
  max-width: 400px;
  margin: 50px auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #f8f8f8;
}

h2 {
  margin-bottom: 20px;
}

form {
  display: flex;
  flex-direction: column;
}

label {
  margin-bottom: 5px;
}

input[type="text"],
input[type="password"],
button {
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
}

button {
  background-color:maroon;
  color: white;
  cursor: pointer;
}

button:hover {
  background-color:maroon;
}

p {
  margin-top: 10px;
}

p a {
  color: #ff4400;
  text-decoration: none;
}

p a:hover {
  text-decoration: underline;
}
.header-container {
  background-color:magenta;
  padding: 10px 0;
  text-align: left;
  color: rgb(255, 255, 255);
}

.app-name {
  font-size: 24px;
  font-weight: bold;
}
.app-logo {
  width: 50px;
  height: 50px;
  margin-right: 10px;
}
.navbar {
  background-color: #007bff;
  padding: 10px 20px;
}

.navbar ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.navbar li {
  margin-right: 10px;
}

.navbar a {
  color: white;
  text-decoration: none;
}

.navbar a:hover {
  text-decoration: underline;
}
/* App Styles */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
}

.header {
  display: flex;
  align-items: center;
  background-color:#007bff; /* Change background color as needed */
  padding: 10px 20px;
}

.logo {
  width: 50px; /* Adjust logo width */
  height: 50px; /* Adjust logo height */
  margin-right: 10px;
}
.pic{
  margin-right: 20px;
  margin: center;
}

.app-name {
  color: white; /* Text color for app name */
}

nav ul {
  list-style-type: none;
  display: flex;
}

nav li {
  margin-left: 10px;
  margin-top: 50px;
}

nav a {
  text-decoration: none;
  color: red
  ; /* Default link color */
}

nav a:hover {
  text-decoration: underline;
  color: #ff7b00; /* Hover color for links */
}

.app {
  background-image: url('https://static.vecteezy.com/system/resources/previews/000/523/561/original/mobile-application-development-on-laptop-screen-concept-background-app-coding-and-web-development-cross-platform-devices-smartphone-tablet-and-computer-vector.jpg');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.home-container {
  color:paleturquoise;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  font-size: 24px; 
 font-weight: lighter;
}

